<template>
  <div class="ling-camera-live">
    <el-row>
      <el-col :span="11">
        <el-button v-if="false" class="refresh" type="primary" @click="refresh">刷新</el-button>
        <div class="prism-player" id="player-camera"></div>
      </el-col>
      <el-col :span="11" :offset="1">
        <el-button v-if="false" class="refresh" type="primary" @click="refresh">刷新</el-button>
        <div class="prism-player" id="player-temp"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      live: {
        camera: `rtmp://${location.hostname}:1935/live/camera`,
        temp: `rtmp://${location.hostname}:1935/live/temp`
      }
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.createCamera()
      this.createTemp()
    })
  },
  methods: {
    createCamera () {
      // eslint-disable-next-line no-new
      const cameraLive = this.live.camera
      this.camera = new Aliplayer({
        'id': 'player-camera',
        'source': cameraLive,
        'width': '100%',
        'height': '500px',
        'autoplay': true,
        'isLive': true,
        'rePlay': false,
        'playsinline': true,
        'preload': true,
        'controlBarVisibility': 'hover'
      }, function (player) {
        console.log('The player is created')
      })
    },
    createTemp () {
      // eslint-disable-next-line no-new
      const tempLive = this.live.temp
      this.temp = new Aliplayer({
        'id': 'player-temp',
        'source': tempLive,
        'width': '100%',
        'height': '500px',
        'autoplay': true,
        'isLive': true,
        'rePlay': false,
        'playsinline': true,
        'preload': true,
        'controlBarVisibility': 'hover'
      }, function (player) {
        console.log('The player is created')
      })
    },
    refresh () {
      this.camera.replay()
      this.temp.replay()
    }
  }
}
</script>

<style lang="scss">
.ling-camera-live {
  .refresh {
    margin-bottom: 10px;
  }
}
</style>



